import { v4 } from 'uuid';

export const TOOL_TEXT = 'text';
export const FILL_TEXT = 'Test WRITE TEXT';

export default (context) => {
    let text = null;
    let imageData = null;

const onMouseDown = (x, y, color, size, fill, filltext) => {
    //alert(filltext);
    text = {
        id: v4(),
        tool: TOOL_TEXT,
        color,
        size,
        fill,
        fillText: filltext?filltext:FILL_TEXT,
        start: { x, y },
        end: null
    };
    imageData = context.getImageData(0, 0, context.canvas.clientWidth, context.canvas.clientHeight);
    return [text];
  };

  const drawText = (item, mouseX, mouseY) => {
    /*var d = document.createElement("span");
    d.font = item.size*10+'px Georgia';
    d.textContent = item.fillText;
    document.body.appendChild(d);
    var emHeight = d.offsetHeight;
    var emWidth = d.offsetWidth;
    document.body.removeChild(d);*/
    const startX = mouseX < item.start.x ? mouseX : item.start.x;
    const startY = mouseY < item.start.y ? mouseY : item.start.y;
    //const widthX = item.start.x +emWidth;
    //const widthY = emHeight;
    
    if (item.size) context.font= item.size*10+'px Georgia';

    const widthX = parseInt(context.measureText(item.fillText).width);
    const widthY = item.size*12;
    //alert(widthX);
    //alert(widthY);

    context.beginPath();
    context.lineWidth = item.size;
    context.strokeStyle = item.color;
    if (item.fill)context.rect(startX, startY, widthX, widthY);
    context.fillStyle = item.fill;
    context.stroke();
    //alert(item.fill);
    //if (item.fill) context.fill();
    //alert(item.size*10+'px Georgia');
    if (item.fill) context.fill();
    if (item.size) context.font= item.size*10+'px Georgia';
    context.fillStyle = item.color;
    if (item.fillText) context.fillText(item.fillText, startX, startY+item.size*10);
  };

  const onMouseMove = (x, y) => {
    if (!text) return;
    context.putImageData(imageData, 0, 0);
    context.save();
    drawText(text, x, y);
    context.restore();
  };

  const onMouseUp = (x, y) => {
    if (!text) return;
    onMouseMove(x, y);
    const item = text;
    imageData = null;
    text = null;
    item.end = { x, y };
    return [item];
  };

  const draw = item => drawText(item, item.end.x, item.end.y);

  return {
    onMouseDown,
    onMouseMove,
    onMouseUp,
    draw,
  };
};
